
@media screen and (max-device-width : 500px)
{
body
  {
    font-size: 48px;
}
}
body {
  background: hsl(60,35%,90%);
}
.invert-text {
  color: hsl(60,35%,90%);
}
.normal-text {
  color: rgba(44, 62, 80, 1);
}
.default-background {
  background: hsl(60,35%,90%);
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: rgba(44, 62, 80, 1);
}



